.bl {
  border-left-style: solid;
  border-left-width: 1px;
}

.b--gold {
  border-color: #ffb700;
}

.bw1 {
  border-width: 0.125rem;
}

.dib {
  display: inline-block;
}

.sans-serif {
  font-family: -apple-system, BlinkMacSystemFont, 'avenir next', avenir,
    'helvetica neue', helvetica, ubuntu, roboto, noto, 'segoe ui', arial,
    sans-serif;
}

.georgia {
  font-family: georgia, serif;
}

.i {
  font-style: italic;
}

.fw1 {
  font-weight: 100;
}

.tracked {
  letter-spacing: 0.1em;
}

.tracked-tight {
  letter-spacing: -0.05em;
}

.lh-title {
  line-height: 1.25;
}

.lh-copy {
  line-height: 1.5;
}

.mw9 {
  max-width: 96rem;
}

.black-80 {
  color: rgba(0, 0, 0, 0.8);
}

.white {
  color: #fff;
}

.bg-black-90 {
  background-color: rgba(0, 0, 0, 0.9);
}

.bg-gold {
  background-color: #ffb700;
}

.pa1 {
  padding: 0.25rem;
}

.pa4 {
  padding: 2rem;
}

.pl4 {
  padding-left: 2rem;
}

.mb2 {
  margin-bottom: 0.5rem;
}

.mb4 {
  margin-bottom: 2rem;
}

.mv0 {
  margin-top: 0;
  margin-bottom: 0;
}

.ttu {
  text-transform: uppercase;
}

.f2 {
  font-size: 2.25rem;
}

.f3 {
  font-size: 1.5rem;
}

.f5 {
  font-size: 1rem;
}

.f6 {
  font-size: 0.875rem;
}

.measure {
  max-width: 30em;
}

.measure-narrow {
  max-width: 20em;
}

.center {
  margin-right: auto;
  margin-left: auto;
}

@media screen and (min-width: 30em) {
  .pt5-ns {
    padding-top: 4rem;
  }

  .f3-ns {
    font-size: 1.5rem;
  }

  .f4-ns {
    font-size: 1.25rem;
  }

  .f5-ns {
    font-size: 1rem;
  }
}

@media screen and (min-width: 30em) and (max-width: 60em) {
  .f1-m {
    font-size: 3rem;
  }
}

@media screen and (min-width: 60em) {
  .mw9-l {
    max-width: 96rem;
  }

  .ph7-l {
    padding-left: 16rem;
    padding-right: 16rem;
  }

  .f-headline-l {
    font-size: 6rem;
  }
}
